.company {
  max-width: 1172px;
  padding-top: 37px;
  padding-bottom: 120px;
  margin: 0 auto;
}

.company__about {
  padding-left: 186px;
  position: relative;
  margin-bottom: 50px;

  &::before {
    content: "";
    display: block;
    width: 220px;
    height: 317px;
    position: absolute;
    background: url("../../img/2005.png") no-repeat;
    left: 6px;
    top: 5px;
  }
}

.company__about h2 {
  @include fonts(400, 36px, 140.6%);
  color: $dark-gray;
  margin-block-start: 0;
  margin-block-end: 20px;
}

.company__about p {
  font-size: 16px;
  line-height: 22px;
  color: $dark-gray;
  margin-block-start: 0;
  margin-block-end: 20px;
}

.company__about-button {
  @include link($blue);
  @include fonts(700, 14px, 16px);
  background-color: transparent;
  border: 1px solid $blue;
  padding: 14px 50px;
  letter-spacing: 0.05em;
  margin-left: 186px;
}

@media (max-width: $tablet) {
  .company {
    @include inner(1023px, 25px);
    padding-top: 11px;
    padding-bottom: 93px;
  }

  .company__about {
    padding-left: 187px;
    position: relative;
    margin-bottom: 52px;

    &::before {
      content: "";
      display: block;
      width: 135px;
      height: 352px;
      position: absolute;
      background: url("../../img/2005.png") no-repeat;
      background-size: contain;
      top: 8px;
      left: 2px;
    }
  }

  .company__about h2 {
    font-size: 32px;
    margin-block-end: 26px;
  }

  .company__about p {
    font-size: 16px;
    line-height: 22px;
    color: $dark-gray;
    margin-block-start: 0;
    margin-block-end: 20px;
  }

  .company__about span {
    display: none;
  }

  .company__about-button {
    margin-left: 187px;
  }
}

@media (max-width: $mobile) {
  .company {
    @include inner(767px, 15px);
    padding-top: 17px;
    padding-bottom: 75px;
  }

  .company__about {
    padding-left: 0;
    position: relative;
    margin-bottom: 40px;

    &::before {
      content: "";
      display: none;
    }
  }

  .company__about h2 {
    font-size: 28px;
    margin-block-end: 21px;
  }

  .company__about p {
    margin-block-end: 39px;
  }

  .company__about-button {
    margin-left: 0;
  }
}
